.azzle-content{
    &-thumb{
        text-align: center;
        position: relative;
        img{
            border-radius: 10px;
            @include max (991px){
                padding-bottom: 40px;
            }
            @include max (767px){
                padding-bottom: 30px;
            }
        }
        &-bg{
            position: absolute;
            bottom: 0;
            z-index: -1;
        }
    }
    &-bg{
        img{
            @include max (991px){
                width: 100%;
            }
        }
    }
    // start there
    &-thumb2{
        img{
            border-radius: 20px;
            border: 5px solid var(--heading-color);
            @include max (991px){
                padding-bottom: 40px;
            }
            @include max (767px){
                padding-bottom: 30px;
            }
        }
    }
}