// fonts variable
$body-font: 'Inter', serif!default;
$heading-font: 'DM Sans', serif!default;
$heading-font2: 'ClashDisplayMedium';
$heading-font3: 'Raleway', serif!default;

// color variable
:root {
  --heading-color: rgba(0, 0, 0, 1);
  --heading-bg: rgba(0, 0, 0, 1);
  --accent-color: rgba(254, 51, 10, 1);
  --accent-bg: rgba(254, 51, 10, 1);
  --light-color: rgba(255, 255, 255, 1);
  --light-color2: rgba(239, 234, 227, 1);
  --light-color3: rgba(219, 214, 207, 1);
  --light-color4: rgba(65, 65, 65, 1);
  --light-color5: rgba(246, 246, 235, 1);
  --light-color6: rgba(18, 18, 18, 1);
  --border-color: rgba(127, 137, 149, 1);
  --variable-color1: rgba(234, 237, 240, 1);
  --variable-color2: rgba(25, 25, 25, 1);
  --variable-color3: rgba(44, 44, 44, 1);
  --variable-color4: rgba(50, 28, 164, 1);
  --variable-color5: rgba(56, 31, 209, 1);
}
$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 1.5em;
$buton_border_radius: 50px;
$button_padding: 18px 26px;
$button_font_weight: 700;
$button_font_family: $body-font;