.azzle-blog{
    &-wrap{
        border-radius: 20px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color: var(--light-color);
        border: 1px solid var(--variable-color1);
        &:hover{
            border: 1px solid var(--light-color);
            box-shadow: 0px 4px 80px 0px rgba(0, 0, 0, 0.05);
            .azzle-blog-thumb img{
                transform: scale(1.1);
            }
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        border-radius: 20px;
        img{
            border-radius: 20px;
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
    }
    &-content{
        padding: 24px 20px;
        h3{
            font-weight: 500;
            font-family: $heading-font2;
        }
    }
    &-meta{
        ul{
            li{
                a{
                    color: var(--light-color4);
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 30px;
                }
            }
        }
    }
    &-data-wrap{
        padding-top: 24px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}