.azzle-iconbox{
    &-wraper{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        @include max (1199px){
            grid-template-columns: repeat(2, 1fr);
            gap: 24px;
        }
        @include max (767px){
            grid-template-columns: repeat(1, 1fr);
        }
    }
    &-icon{
        padding-bottom: 30px;
        transition: all 0.4s;
    }
    &-content{
        h3{
            padding-bottom: 15px;
        }
        transition: all 0.4s;
    }
    &-wrap{
        padding: 30px 20px;
        transition: all 0.4s;
        border: 2px solid var(--heading-color);
        @include max (1199px){
            border-radius: 10px;
        }
        &:hover{
            border: 2px solid transparent;
            background-color: var(--heading-color);
            .azzle-iconbox-icon{
                path{
                    fill: var(--accent-bg);
                }
            }
            .azzle-iconbox-content{
                h3,p{
                    color: var(--variable-color1);
                }
            }
            .azzle-iconbox-button{
                a{
                    path{
                        stroke: var(--accent-color);
                        fill: var(--accent-color);
                    }
                }
            }
        }
        &.wrap1{
            border-radius: 10px 0 0 10px;
            @include max (1199px){
                border-radius: 10px;
            }
        }
        &.wrap2{
            border-radius: 0 10px 10px 0;
            @include max (1199px){
                border-radius: 10px;
            }
        }
    }
    &-rating{
        padding-bottom: 30px;
    }
    &-content2{
        padding-bottom: 30px;
        p{
            font-size: 18px;
            line-height: 1.67em;
            color: var(--light-color);
        }
    }
    &-author-wrap{
        display: flex;
        align-items: center;
        gap: 12px;
    }
    &-author-thumb{
        img{
            border-radius: 100px;
        }
    }
    &-author-data{
        p{
            font-weight: 600;
            font-size: 18px;
            line-height: 30px;
            color: var(--light-color);
            margin-bottom: 0;
        }
        span{
            font-size: 14px;
            line-height: 1.43em;
            color: var(--light-color);
        }
    }
    &-wrap2{
        padding: 30px 20px;
        margin-bottom: 24px;
        border: 2px solid var(--variable-color2);
        border-radius: 10px;        
    }
    // end two
    &-wrap3{
        margin-bottom: 24px;
        @include max (767px){
            padding-right: 170px;
        }
        @include max (575px){
            padding-right: 0;
        }
    }
    &-icon3{
        width: 80px;
        height: 80px;
        display: flex;
        align-items: center;
        border-radius: 100px;
        justify-content: center;
        background-color: var(--light-color);
        box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.1);
    }
    &-content3{
        h3{
            padding-bottom: 15px;
            font-weight: 500;
            font-family: $heading-font2;
        }
        p{
            font-size: 18px;
            line-height: 1.67em;
        }
    }
    // end three
    &-wrap4{
        margin-bottom: 24px;
        padding: 24px 13px;
    }
    &-icon4{
        padding-bottom: 30px;
    }
    &-content4{
        h3{
            padding-bottom: 20px;
            font-family: $heading-font3;
        }
        p{
            font-size: 18px;
            line-height: 1.67em;
        }
    }
    // end four
    &-wrap5{
        padding: 30px;
        margin-bottom: 24px;
        border-radius: 10px; 
        background-color: var(--light-color6);
        @include max (767px){
            padding: 30px 20px;
        }
    }
    &-content5{
        h3{
            padding: 30px 0 24px 0;
            font-family: $heading-font3;
            font-size: 30px;
            line-height: 1.5em;
            color: var(--light-color5);
        }
        p{
            font-size: 24px;
            line-height: 1.43em;
            color: var(--light-color5);
            padding-bottom: 30px;
        }
    }
}
.divider3{
    padding-top: 130px;
    border-bottom: 1px solid var(--variable-color1);
    margin-bottom: 130px;
    @include max (991px){
        padding-top: 80px;
        margin-bottom: 80px;
    }
    @include max (767px){
        padding-top: 0;
        margin-bottom: 60px;
    }
}