.azzle-hero{
    &-section{
        position: relative;
        border-radius: 0 0 50px 50px;
        background-color: var(--light-color2);
        padding: 195px 0 130px;
        @include max (991px){
            padding: 170px 0 100px;
        }
        @include max (767px){
            padding: 150px 0 80px;
        }
        @include max (575px){
            padding: 130px 0 60px;
        }
    }
    &-content1{
        max-width: 980px;
        text-align: center;
        margin: 0 auto;
        margin-bottom: 80px;
        position: relative;
        z-index: 1;
        @include max (991px){
            margin-bottom: 60px;
        }
        @include max (767px){
            margin-bottom: 40px;
        }
        h1{
            padding-bottom: 24px;
        }
        p{
            font-weight: 600;
        }
    }
    &-dashboard{
        position: relative;
        z-index: 1;
    }
    &-button{
        display: flex;
        gap: 24px;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    // end one

    &-section2{
        padding: 210px 0 300px;
        @include max (991px){
            padding: 180px 0 230px;
        }
        @include max (767px){
            padding: 150px 0 180px;
        }
        @include max (575px){
            padding: 180px 0 180px;
        }
    }
    &-content2{
        padding-right: 50px;
        h1{
            font-size: 100px;
            font-weight: 500;
            line-height: 1em;
            padding-bottom: 24px;
            color: var(--light-color);
            font-family: $heading-font2;
            @include max (1399px){
                font-size: 90px;
            }
            @include max (1199px){
                font-size: 75px;
            }
            @include max (767px){
                font-size: 65px;
            }
            @include max (575px){
                font-size: 42px;
            }
        }
        p{
            color: var(--light-color);
            font-size: 18px;
            line-height: 1.67em;
            margin: 0;
        }
    }
    &-thumb{
        position: relative;
        text-align: center;
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    &-icon {
        position: absolute;
        top: 28%;
        left: 21px;
        @include max (991px){
            top: 25%;
            left: 15%;
        }
    }
    // endtwo
    &-section3{
        padding: 195px 0 130px;
        @include max (991px){
            padding: 170px 0 100px;
        }
        @include max (767px){
            padding: 150px 0 80px;
        }
        @include max (575px){
            padding: 130px 0 60px;
        }
    }
    &-content3{
        h1{
            font-family: $heading-font3;
            font-weight: 700;
            font-size: 120px;
            line-height: 1em;
            letter-spacing: -1px;
            @include max (1399px){
                font-size: 100px;
            }
            @include max (1199px){
                font-size: 90px;
            }
            @include max (991px){
                padding-bottom: 20px;
            }
            @include max (767px){
                font-size: 70px;
            }
            @include max (575px){
                font-size: 55px;
            }
        }
        p{
            font-weight: 600;
            font-size: 24px;
            line-height: 1.33em;
            letter-spacing: 0px;
        }
    }
    &-thumb3{
        padding: 0 50px;
        @include max (991px){
            padding: 0 30px;
        }
        img{
            width: 100%;
            border-radius: 20px;
            border: 10px solid var(--heading-color);
        }
    }
}
.divider{
    padding-top: 80px;
    border-bottom: 1px solid var(--light-color3);
    margin-bottom: 80px;
    @include max (767px){
        padding-top: 60px;
        margin-bottom: 60px;
    }
    @include max (575px){
        padding-top: 40px;
        margin-bottom: 40px;
    }
    
}