.breadcrumb{
  &-wrapper{
    text-align: center;
    padding: 200px 0 130px;
    position: relative;
    transition: all 0.4s;
    background-color: var(--heading-bg);
    @include max(1199px) {
      padding: 180px 0 100px;
    }
    @include max(767px) {
      padding: 120px 0 80px;

    }
  }
  &-content {
    text-align: center;
    margin: 0 auto;
    h1{
        color: var(--light-color);
    }
    p{
      font-size: 21px;
    }
  }

  &-menu{
    ul{
      li{
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        display: inline-block;
        padding: 0 6px;
        color: var(--light-color);
        a{
            color: var(--light-color);
            font-weight: 400;
            font-size: 24px;
            line-height: 32px;
        }
      }
    }
    &-wrap{
      padding-top: 50px;
      @include max (1199px){
        padding-top: 30px;
      }
    }
  }
}


