.azzle-header{
  &-section {
    transition: all 0.4s;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: auto;
    z-index: 10;
    text-align: center;
    @include max (991px){
      padding: 20px 0;
    }
    &.section2{
      background-color: var(--heading-bg)!important;
    }
  }
  &-button-wraper{
    display: flex;
    gap: 24px;
    align-items: center;
    @include max (767px){
      display: none;
    }
  }
  &-login-button{
    ul{
      li{
        a{
          font-weight: 700;
          font-size: 16px;
          line-height: 1.5em;
          text-align: center;
          color: var(--heading-color);
          vertical-align: middle;
          padding: 14px 24px;
          border-radius: 50px;
          border: 1px solid var(--border-color);
        }
      }
    }
    &.button2{
      ul{
        li{
          a{
            color: var(--light-color);
            border: none;
            padding: 0;
            border-radius: 0;
            font-weight: 700;
            font-size: 16px;
            line-height: 1.5em;
          }
        }
      }
    }
    &.button3{
      ul{
        li{
          a{
            color: var(--heading-color);
            border: none;
            padding: 0;
            border-radius: 0;
            font-weight: 700;
            font-size: 16px;
            line-height: 1.5em;
          }
        }
      }
    }
  }
}
.menu-left{
