// end main




.text-align-left{
  text-align: left;
}

.main-menu {
  a {
    color: var(--heading-color);
    font-weight: 600;
    position: relative;
    display: block;
    transition: all 0.4s;
    text-transform: capitalize;
    font-size: 16px;
    line-height: 1.5em;
    &.light-color{
      color: var(--light-color);
    }
  }
  >ul {
    >li {
      margin: 0 17px;
      @include max (1199px){
        margin: 0 10px;
      }
    }
  }


  ul {
    margin: 0;
    padding: 0;

    li {
      list-style-type: none;
      display: inline-block;
      position: relative;
      &.menu-item-has-children {
        
        >a {
          &:after {
            content: "\ea4d";
            position: relative;
            margin-left: 2px;
            top: 2px;
            font-size: 20px;
            display: inline-block;
            font-family: 'remixicon';
            transition: all 0.4s ease-in-out;
          }
          &.light-color{
            color: var(--light-color);
          }
        }
      }
      &:hover{
        &.menu-item-has-children {
          >a {
            &:after {
              transform: rotate(-180deg);
            }
            
          }
        }
      }
      &:last-child {
        margin-right: 0;
      }

      &:first-child {
        margin-left: 0;
      }

      &:hover {

        >ul.sub-menu {
          visibility: visible;
          opacity: 1;
          z-index: 99;
          pointer-events: visible;
          transform: translateY(0);
        }
      }

    }
  }

  
  ul.sub-menu {
    z-index: 500;
    background-color: #fff;
    box-shadow: -2px 2px 70px -25px rgb(0 0 0 / 30%);
    position: absolute;
    left: 0;
    top: 90px;
    margin: 0;
    padding: 12px 0;
    transition: .4s;
    z-index: 4;
    transform: translatey(20px);
    opacity: 0;
    text-align: left;
    visibility: hidden;
    min-width: 220px;
    border-radius: 6px;
    
    li {
      padding: 5px 10px 5px 20px;
      a {
        font-size: 18px;
        line-height: 30px;
        display: inline-block;
        position: relative;
        font-weight: 400;
        text-transform: capitalize;
        
      }
      &.menu-item-has-children  {
        a.no-border{
          display: block;
          &::after{
            position: absolute;
            right: 0;
          }
          &::before{
            content: none;
          }
        }
      }
      ul.sub-menu {
        transition: all 0.4s;
        margin-left: 0px!important;
        transform: translateY(30px);
      }
      &:hover ul.sub-menu{
        transform: translateY(0px);
      }
    } 
  }
  ul.sub-menu {
    padding: 12px 0;
    left: -27px;
    top: 86px;

    
    li {
      display: block;
      margin: 0 0;
      transition: all 0.4s;

      ul.sub-menu {
        left: 100%;
        right: auto;
        top: 0;
        margin: 0 0;
        margin-left: 20px;
        border-radius: 10px;
        li {
          ul {
            left: 100%;
            right: auto;
          }
        }
      }
    }
  }
}


ul.sub-menu{
  li{
    a{
      color: var(--heading-color)!important;
      &::before{
        content: '';
        position: absolute;
        bottom: -3px;
        left: auto;
        right: 0;
        height: 2px;
        width: 0;
        background-color: var(--heading-color)!important;
        transition: all 0.3s ease;
        @include max (767px){
          height: 0;
        }
      }
      &:hover::before{
        left: 0;
        right: auto;
        width: 100%;
        @include max (991px){
          display: none;
        }
      }
    }
  }
}


.menu-style1 {
  >ul {
    >li {
      >a {
        padding: 30px 0;
      }
    }
  }
}



// barger icon

.site-header .azzle-menu-toggle {
  display: flex;
  height: 30px;
  width: 30px;
  margin-left: 40px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
}
.site-header .azzle-menu-toggle span {
  display: block;
  height: 3px;
  background-color: var(--heading-color);
  width: 30px;
  position: relative;
}
.toggle2 span{
  background-color: var(--light-color)!important;
}
.reveal-header .azzle-menu-toggle span {
  background-color: var(--heading-color) !important;
}
.site-header .azzle-menu-toggle span:before,
.site-header .azzle-menu-toggle span:after {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--heading-color);
}
.reveal-header .azzle-menu-toggle span:before,
.reveal-header .azzle-menu-toggle span:after {
  background-color: var(--heading-color) !important;
}
.dark-mode-texts .azzle-menu-toggle span {
  background-color: var(--heading-color);
}
.dark-mode-texts .azzle-menu-toggle span:before,
.dark-mode-texts .azzle-menu-toggle span:after {
  background-color: var(--heading-color);
}
.site-header .azzle-menu-toggle span:before {
  top: -10px;
}
.site-header .azzle-menu-toggle span:after {
  top: 9px;
}

.site-header .mobile-menu-trigger.dark-color span {
  background-color: var(--heading-color);
}

.site-header .mobile-menu-trigger.dark-color span::after, 
.site-header .mobile-menu-trigger.dark-color span::before {
  background-color: var(--heading-color);
}

.site-header .mobile-menu-trigger.dark-color span::after, 
.site-header .mobile-menu-trigger.dark-color span::before {
  background-color: var(--heading-color);
}

.site-header .azzle-menu-toggle.toggle2 span:before,
.site-header .azzle-menu-toggle.toggle2 span:after
{
  background-color: var(--light-color);
}

