.azzle-listicon{
    &-wrap{
        ul{
            li{
                display: flex;
                gap: 12px;
                margin-bottom: 24px;
                h3{
                    padding-bottom: 0;
                }
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
    &-wrap2{
        ul{
            li{
                margin-bottom: 24px;
                position: relative;
                padding-left: 30px;
                &::before{
                    width: 14px;
                    height: 14px;
                    content: "";
                    top: 8px;
                    left: 0;
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--variable-color4);
                }
                &:last-child{
                    margin-bottom: 0;
                }
                h3{
                    padding-bottom: 0;
                    font-weight: 500;
                    font-family: $heading-font2;
                }
            }
        }
    }
}


.azzle-int-slider-init2{
    margin-top: 24px;
    &.slick-initialized.slick-slider {
        direction: rtl;
    }
}
.azzle-int-slider-wraper{
    position: relative;
    padding-top: 130px;
    @include max (1399px){
        padding-top: 60px;
    }
    @include max (991px){
        padding-top: 80px;
        padding-bottom: 40px;
    }
    @include max (767px){
        padding-top: 60px;
        padding-bottom: 40px;
    }
}
.azzle-int-bg {
    position: absolute;
    top: 0;
    @include max (991px){
        display: none;
    }
    
}