.azzle-hero-shape {
    position: absolute;
    top: 17%;
    right: 0;
    @include max (767px){
        display: none;
    }
}
.azzle-footer{
    &-shape{
        position: absolute;
        top: 0;
        right: 0;
        @include max (767px){
            display: none;
        }
        img{
            border-radius: 0 50px 0 0;
        }
    }
}
.azzle-pricing-shape {
    position: absolute;
    top: -35%;
    left: 0;
    z-index: -1;
    @include max (767px){
        display: none;
    }
}