.azzle-section-padding{
    padding: 130px 0;
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.azzle-section-padding2{
    padding: 130px 0 106px;
    @include max (991px){
        padding: 80px 0 56px;
    }
    @include max (767px){
        padding: 60px 0 36px;
    }
}

.azzle-section-padding3{
    padding: 130px 0 176px;
    @include max (991px){
        padding: 80px 0 156px;
    }
    @include max (767px){
        padding: 60px 0 136px;
    }
}
.azzle-section-padding4{
    padding: 50px 0 0;
    @include max (991px){
        padding: 40px 0 0;
    }
    @include max (767px){
        padding: 30px 0 0;
    }
}



// section title

.azzle-section-title{
    padding-bottom: 80px;
    @include max (991px){
        padding-bottom: 60px;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
    &.max-w-650{
        max-width: 650px;
    }
    &.center{
        max-width: 650px;
        margin: 0 auto;
        text-align: center;
        &.max-width-700{
            max-width: 700px;
        }
        &.max-width-750{
            max-width: 750px;
        }
    }
    &.max-width-780{
        max-width: 780px;
    }
    &.max-w-800{
        max-width: 880px;
    }
    &.max-w-1000{
        max-width: 1000px;
    }
    &.font-title{
        h2{
            font-family: $heading-font2;
            font-weight: 500;
        }
    }
    &.title2{
        h2{
            color: var(--light-color);
        }
    }
    &.title3{
        h2{
            font-family: $heading-font3;
            font-weight: 700;
            font-size: 80px;
            line-height: 1em;
            letter-spacing: -1px;
            @include max (1199px){
                font-size: 70px;
            }
            @include max (991px){
                font-size: 55px;
            }
            @include max (767px){
                font-size: 48px;
            }
        }
    }
}


// default content
.azzle-default-content{
    h2{
        padding-bottom: 20px;
    }
    h3{
        padding-bottom: 30px;
    }
    &.content2{
        h2,p{
            color: var(--light-color);
        }
        ul{
            li{
                h3{
                    color: var(--light-color);
                }
            }
        }
    }
    &.content3{
        h2{
            font-family: $heading-font2;
            font-weight: 500;
        }
        p{
            font-size: 18px;
            line-height: 1.67em;
        }
    }
    &.content4{
        h3{
            font-family: $heading-font2;
            font-weight: 500;
        }
        p{
            font-size: 24px;
            line-height: 1.67em;
        }
    }
    &.content5{
        h2{
            font-family: $heading-font3;
        }
        p{
            font-size: 24px;
            line-height: 1.67em;
            span{
                font-weight: 600;
                color: var(--variable-color5);
            }
        }
    }
}
.pl-110{
    padding-left: 110px;
    @include max (1399px){
        padding-left: 50px;
    }
    @include max (991px){
        padding-left: 0;
    }
}
.pr-110{
    padding-right: 110px;
    @include max (1399px){
        padding-right:50px;
    }
    @include max (767px){
        padding-right:0px;
    }
}
.mt-50{
    margin-top: 50px;
    @include max (991px){
        margin-top: 40px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.section{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}