
.azzle-default-btn,
#azzle-submit-btn {
  font-size: $button_font_size;
  line-height: $buton_line_height;
  padding: $button_padding;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  border-radius: $buton_border_radius;
  color: var(--light-color);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  position: relative;
  transition: all 0.4s;
  background-color: var(--heading-bg);
  @include max (767px){
    padding: 15px 24px;
  }
  &.btn2{
    background-color: var(--variable-color4);
    align-items: center;
    gap: 10px;
  }
  &.btn3{
    background-color: var(--variable-color4);
    align-items: center;
    gap: 10px;
    padding: 18px 35px;
  }
  &.btn4{
    background-color: var(--variable-color4);
    align-items: center;
    gap: 10px;
    padding: 13px 28px;
  }
  &:hover{
    color: var(--light-color);
  }
  &.azzle-header-btn{
    padding: 14px 24px;
  }
  &.outline-btn{
    padding: 16px 34px;
    color: var(--heading-color);
    border: 2px solid var(--heading-color);
    background-color: var(--light-color2);
    &.btn2{
      color: var(--heading-color);
      background-color: var(--accent-color);
    }
  }
  &.outline-btn3{
    width: 100%;
    padding: 16px 34px;
    color: var(--heading-color);
    border: 2px solid var(--heading-color);
    background-color: var(--light-color5);
    justify-content: center;
  }
  &.d-block {
    width: 100%;
  }
  &:focus {
    box-shadow: none;
  }
}


