input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 50px;
  border-radius: 50px;
  background-color: transparent;
  padding: 13px 70px 13px 20px;
  border: 1px solid var(--heading-color);
  transition: all 0.4s;
  font-size: 16px;
  line-height: 24px;
  &::placeholder{
    color: var(--color-variable3);
  }
}
textarea {
  height: 180px;
  resize: none;
  @include max(767px) {
    height: 130px;
  }
}
.azzle-main{
  &-field{
    position: relative;
  }
  &-field2{
    position: relative;
    input{
      width: 500px;
      height: 60px;
      color: var(--light-color);
      padding: 13px 190px 13px 20px;
      border-radius: 50px!important;
      border: 1px solid var(--light-color)!important;
      @include max (767px){
        width: 100%;
      }
      &::placeholder{
        color: var(--light-color);
      }
    }
  }
}

.sub-btn {
  width: 60px;
  height: 40px;
  background-color: #000;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 5px;
  right: 5px;
}
.sub-btn2{
  position: absolute!important;
  top: 5px;
  right: 145px;
  @include max (991px){
    right: 105px;
  }
  @include max (767px){
    right: 5px;
  }
}