.azzle-faq{
    &-wrap1{
      padding-left: 40px;
      @include max (991px){
        padding-left: 0;
        padding-top: 40px;
      }
      @include max (767px){
        padding-top: 30px;
      }
      &.wrap2{
        @include max (767px){
          padding-top: 0;
        }
      }
    }
    &-item{
      padding-bottom: 24px;
      border-bottom: 1px solid var(--light-color3);
      margin-bottom: 24px;
      &:last-child{
          border-bottom: none;
          padding-bottom: 0;
          margin-bottom: 0;
      }
      &.open {
        .azzle-active-icon{
          img{
              transform: rotate(36deg);
          }
        }
        .azzle-faq-header{
          padding-bottom: 10px;
        }
        .azzle-faq-body{
          display: block;
          padding-right: 110px;
          @include max (991px){
            padding-right: 80px;
          }
          @include max (767px){
            padding-right: 60px;
          }
          p{
            font-size: 18px;
            line-height: 1.67em;
            color: var(--variable-color3);
          }
        }
      }
      &.item2{
        padding-bottom: 0;
        border: none;
        margin-bottom: 24px;
        padding: 24px;
        border-radius: 20px;
        border: 1px solid var(--variable-color1);
        &.open{
          box-shadow: 0px 4px 60px 0px rgba(0, 0, 0, 0.1);
          border: 1px solid transparent;
          background-color: var(--light-color);
          .azzle-active-icon{
            width: 50px;
            height: 50px;
            border-radius: 100px;
            background-color: var(--variable-color4);
          }
        }
        &:last-child{
          margin-bottom: 0;
        }
      }
    }
    &-header{
      cursor: pointer;
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      transition: all 0.4s;
      h3{
        @include max(767px) {
          font-size: 24px;
          padding-right: 25px;
        }
      }
      &.header2{
        h3{
          font-weight: 500;
          font-family: $heading-font2;
        }
      }
    }
    &-body{
      display: none;
    }
}

// 

.azzle-active-icon{
  transition: all .4s;
  display: flex;
  width: 50px;
  flex-shrink: 0;
  height: 50px;
  padding: 5px;
  line-height: 31px;
  text-align: center;
  align-items: center;
  border-radius: 6px;
  justify-content: center;
  background-color: var(--accent-bg2);
  @include max (767px){
    width: 40px;
    height: 40px;
  }
  img{
    transition: all 0.4s;
    width: 25px;
  }
}



