.azzle-popup-video {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.azzle-video{
    &-section{
        margin: 0 120px;
        border-radius: 50px;
        background-color: var(--heading-bg);
        @include max (1399px){
            margin: 0 80px;
        }
        @include max (1199px){
            margin: 0 50px;
        }
        @include max (767px){
            margin: 0 30px;
        }
    }
    &-thumb{
        position: relative;
        img{
            border-radius: 20px 0 0 20px;
            @include max (991px){
                border-radius: 20px!important;
                width: 100%;
            }
        }
    }
    &-content{
        max-width: 700px;
        @include max (1800px){
            padding-left: 0!important;
            padding: 100px 50px 100px 50px!important;
        }
         @include max (1399px){
            padding: 100px 30px 100px 30px!important;
        }
        @include max (991px){
            padding: 60px 50px 60px 50px!important;
        }
        @include max (767px){
            padding: 60px 30px 60px 30px!important;
        }
        h2{
            color: var(--light-color);
            margin-bottom: 24px;
        }
        p{
            color: var(--variable-color1);
            margin: 0;
        }
    }
}
.divider2{
    padding-top: 50px;
    border-bottom: 1px solid var(--variable-color2);
    margin-bottom: 50px;
    @include max (991px){
        padding-top: 40px;
        border-bottom: 1px solid var(--variable-color2);
        margin-bottom: 40px;
    }
    @include max (767px){
        padding-top: 30px;
        border-bottom: 1px solid var(--variable-color2);
        margin-bottom: 30px;
    }
}

.pl-50{
    padding-left: 50px;
}

.waves {
    position: absolute;
    width: 210px;
    height: 210px;
    background: var(--accent-bg);
    opacity: 0;
    border-radius: 100%;
    left: -45px;
    top: -45px;
    z-index: 0;
    transform: translate(-50px, -50px);
    -webkit-animation: waves 3s ease-in-out infinite;
    animation: waves 3s ease-in-out infinite;
    z-index: -1;
    @include max(991px) {
      width: 160px;
      height: 160px;
      left: -20px;
      top: -20px;
    }
    &.wave-1 {
      -webkit-animation-delay: 0s;
      animation-delay: 0s;
    }
    &.wave-2 {
      -webkit-animation-delay: 1s;
      animation-delay: 1s;
    }
    &.wave-3 {
      -webkit-animation-delay: 2s;
      animation-delay: 2s;
    }
  
}
  @keyframes waves {
    0% {
        -webkit-transform: scale(0.2, 0.2);
        transform: scale(0.2, 0.2);
        opacity: 0;
    }
  
    50% {
        opacity: 0.9;
    }
  
    100% {
        -webkit-transform: scale(0.9, 0.9);
        transform: scale(0.9, 0.9);
        opacity: 0;
    }
}
