.azzle-cta-slider{
    &-title{
        font-size: 120px;
        line-height: 1em;
        font-weight: 700;
        @include max(1399px) { 
            font-size: 100px;
        } 
        @include max(991px) { 
            font-size: 80px;
        }
        @include max(767px) { 
            font-size: 60px;
        }
        @include max(575px) { 
            font-size: 42px;
        }
    }
    &-item{
        display: flex!important;
        gap: 24px;
        align-items: center;
        white-space: nowrap;
        width: 1400px !important;
        @include max (1399px){
            width: 1200px !important;
        }
        @include max (991px){
            width: 960px!important;
        }
        @include max (767px){
            width: 750px!important;
        }
        @include max (575px){
            width: 550px!important;
        }
        img{
            @include max (767px){
                width: 40px;
            }
        }
    }
}

.azzle-cta-slider-init.slick-initialized.slick-slider {
    height: 134px !important;
    overflow: hidden;
    @include max (1399px){
        height: 108px !important;
    }
    @include max (991px){
        height: 85px !important;
    }
    @include max (767px){
        height: 42px!important;
    }
}

