.azzle-title-pricing-btn{
    text-align: center;
}
.toggle-switch {
    cursor: pointer;
    display: inline-block;
    width: 300px;
    height: 60px;
    display: flex;
    margin: 0 auto;
    align-items: center;
    border-radius: 50px;
    justify-content: center;
    border: 2px solid var(--heading-color);
    span{
        border-radius: 30px;
        font-family: $heading-font;
        font-size: 16px;
        font-weight: 700;
        line-height: 24px;
        text-align: center;
    }
}
.toggle-switch input {
    display: none;
}

.toggle-switch input:checked + span + span,
.toggle-switch input + span {
    background-color: var(--heading-bg);
    padding: 18px 48px;
    color: var(--light-color);
}
.toggle-switch input + span + span,
.toggle-switch input:checked + span {
    background-color: transparent;
    border-color: transparent;
    padding: 18px 51px;
    color: var(--heading-color);
  }
// end switcher btn

.azzle-pricing{
    &-wrap{
        padding: 40px;
        border-radius: 10px;
        position: relative;
        transition: all 0.4s;
        margin-bottom: 24px;
        z-index: 0;
        background-color: var(--light-color2);
        @include max (991px){
            padding: 40px;
        }
        @include max (767px){
            padding: 30px;
        }
        &.active{
            background-color: var(--heading-color);
            h2,h3,p,span{
                color: var(--light-color);
            }
        }
        &.pricing-active{
            border: 0;
            &::before{
                content: "";
                right: 0;
                bottom: 0;
                width: 100%;
                height: 100%;
                z-index: -1;
                position: absolute;
                background-size: 100% 100%;
                background-repeat: no-repeat;
                background-image: url(../images/v1/pricing-shape.png);
        
            }
            .azzle-pricing-price{
                background: var(--accent-bg);
            }
            .shape-hide{
                opacity: 1;
            }
            .azzle-pricing-body{
                background: var(--accent-bg);
                ul{
                    li{
                        color: var(--white-color);
                        path{
                            fill: var(--white-bg);
                        }
                    }
                }
            }
        }
    }
    &-header{
        padding-bottom: 24px;
        border-bottom: 1px solid var(--light-color3);
        margin-bottom: 24px;
        h3{
            padding-bottom: 10px;
        }
        p{
            font-size: 18px;
            line-height: 1.5em;
        }
    }
    &-price{
        position: relative;
        transition: all 0.4s;
        display: flex;
        gap: 15px;
        align-items: end;
        padding-bottom: 15px;
        span{
            color:var(--heading-color);
            font-size: 18px;
            line-height: 1.5em;
            display: inline-block;
        }
    }
    &-body{
       p{
        font-size: 18px;
        line-height: 1.5em;
       }
    }
}
