.azzle-t{
    &-wrap{
        display: grid!important;
        gap: 135px;
        align-items: center;
        grid-template-columns: 416px 1fr;
        @include max (1199px){
            gap: 10%;
        }
        @include max (991px){
            display: block!important;
        }
    }
    &-thumb{
        flex-shrink: 0;
        img{
            border-radius: 10px;
        }
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
    &-author-title{
        p{
            font-weight: 600;
            margin-bottom: 5px;
        }
        span{
            font-size: 18px;
            line-height: 30px;
        }
    }
}

.azzle-t-slider-init .azzle-t-slider-next, 
.azzle-t-slider-init .azzle-t-slider-prev {
    bottom: -100px;
    right: 54%;
    width: 50px;
    height: 50px;
    z-index: 1;
    display: flex;
    cursor: pointer;
    position: absolute;
    transition: all .4s;
    align-items: center;
    border-radius: 100px;
    background-size: 20px;
    justify-content: center;
    transform: translateY(-50%);
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--variable-color1);
    background-image: url(../images/home2/arrow-black.svg);
    @include max (991px){
        right: 92%;
    }
    @include max (767px){
        display: none!important;
    }
}
.azzle-t-slider-init .azzle-t-slider-prev {
    left: auto;
    right: 49%;
    bottom: -75px;
    transform: rotate(180deg);
    background-image: url(../images/home2/arrow-black.svg);
     @include max (991px){
        right: 82%;
    }
}   
