.azzle-footer{
  &-section{
    padding: 130px 0 0;
    position: relative;
    border-radius: 50px 50px 0 0;
    background-color: var(--light-color2);
    position: relative;
    z-index: 1;
    margin-top: -50px;
    @include max (991px){
      padding: 80px 0 0;
    }
    @include max (767px){
      padding: 60px 0 0;
      border-radius: 30px 30px 0 0;
    }
  }
  &-one{
    padding-top: 100px;
    border-top: 1px solid var(--light-color3);
    margin-top: 100px;
    padding-bottom: 100px;
    border-bottom: 1px solid var(--light-color3);
     @include max (991px){
      padding-top: 80px;
      margin-top: 80px;
      padding-bottom: 80px;
    }
    @include max (767px){
      padding-top: 60px;
      margin-top: 60px;
      padding-bottom: 60px;
    }
  }
  &-menu{
    &.pl-30{
      padding-left: 30px;
      @include max (1199px){
        padding-left: 0;
      }
    }
    &.pl-70{
      padding-left: 70px;
      @include max (1199px){
        padding-left: 20px;
      }
      @include max (767px){
        padding-left: 0px;
      }
    }
    @include max (767px){
      margin-bottom: 30px;
    }
    h4{
      font-weight: 600;
      font-family: $body-font;
      margin-bottom: 24px;
      @include max (767px){
        margin-bottom: 18px;
      }
    }
    ul{
      li{
        margin-bottom: 10px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          font-size: 18px;
          line-height: 1.67em;
          position: relative;
          transition: all 0.4s;
          color: var(--variable-color2);
          &::before{
            content: " ";
            left: 0;
            bottom: -3px;
            width: 0;
            height: 1px;
            position: absolute;
            transition: all .4s;
            background: var(--variable-color2);
          }
          &:hover::before{
            width: 100%;
          }
        }
      }
    }
    &.menu2{
      h4{
        color: var(--light-color);
      }
      ul{
          li{
            a{
              color: var(--light-color);
              &::before{
                background: var(--light-color);
              }
            }
          }
        }
    }
  }
  &-textarea{
    padding-right: 10px;
    @include max (1199px){
      margin-bottom: 50px;
      max-width: 450px;
    }
    @include max (991px){
      margin-bottom: 40px;
    }
    @include max (767px){
      margin-bottom: 30px;
    }
    img{
      padding-bottom: 30px;
    }
    p{
      color: var(--variable-color2);
      margin-bottom: 24px;
      font-size: 18px;
      line-height: 1.67em;
    }
    a{
      color: var(--variable-color2);
      span{
        font-weight: 600;
      }
    }
  }
  &-bottom-text{
    padding: 35px 0;
    text-align: center;
    p{
      font-size: 18px;
      line-height: 25px;
    }
  }
  // end one
  &-section2{
    padding: 155px 0 0;
  }
  &-two{
    padding-top: 100px;
    padding-bottom: 60px;
    @include max (991px){
      padding-top: 80px;
      padding-bottom: 40px;
    }
    @include max (767px){
      padding-top: 60px;
      padding-bottom: 30px;
    }
  }
  &-bottom-text-wrap{
    display: flex;
    align-items: center;
    padding: 35px 0;
    border-top: 1px solid var(--light-color);
    justify-content: space-between;
    @include max (767px){
      display: block;
      text-align: center;
    }
  }
  &-bottom-text2{
    p{
      color: var(--light-color);
    }
  }
  &-bottom-logo{
    @include max (767px){
      padding-bottom: 15px;
    }
  }
}

.pl-170{
 padding-left: 170px;
 @include max (1399px){
  padding-left: 120px;
 }
  @include max (1199px){
  padding-left: 80px;
 }
 @include max (991px){
  padding-left: 35px;
  padding-top: 40px;
 }
 @include max (767px){
  padding-left: 0;
    padding-top: 0;
  }
}
.pl-101{
  padding-left: 100px;
  @include max (1399px){
    padding-left: 80px;
  }
  @include max (1199px){
    padding-left: 45px;
  }
  @include max (991px){
    padding-left: 0;
    padding-top: 40px;
  }
  @include max (767px){
    padding-top: 0;
  }
}
.pl-51{
  padding-left: 50px;
  @include max (1199px){
    padding-left: 35px;
  }
  @include max (767px){
    padding-left: 0;
  }
  
}