// counter

.azzle-counter{
  &-wrap {
    display: flex;
    gap: 80px;
    align-items: center;
    flex-wrap: wrap;
    @include max (1800px){
      gap: 50px;
    }
    @include max (1660px){
      gap: 35px;
    }
  }
  &-item {
    p{
      padding-top: 12px;
      font-weight: 600;
      @include max (1499px){
        padding-top: 0;
      }
    }
  }
  &-data {
    position: relative;
    display: flex;
    overflow: hidden;
    align-items: center;
    height: 120px;
    font-size: 120px !important;
    margin: 0!important;
    color: var(--accent-color)!important;
    @include max(1599px) { 
      font-size: 100px !important;
    }
    @include max (1199px){
      height: 100px;
    }
    @include max(991px) { 
      font-size: 80px !important;
      height: 80px;
    }
    @include max(767px) { 
        font-size: 60px !important;
        height: 60px;
    }
    @include max(575px) { 
        font-size: 42px !important;
        height: 42px;
    }
  }
}


.azzle-counter-data > span {
  z-index: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: transform 2s ease;
  transform: translateY(0);
  line-height: 1;
  
}
.data2 > span{
  width: 15%;
  @include max (1199px){
    width: 10%;
  }
}
.azzle-counter-data > span span {
  flex: 0 0 100%;
  height: 100%;
}
.azzle-counter-data:nth-child(1) > span {
  transition-delay: 0s;
}
