.azzle-social{
    &-wrap{
        ul{
            li{
                display: flex;
                align-items: center;
                gap: 15px;
                transition: all 0.4s;
                font-size: 21px;
                line-height: 28px;
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                &:hover{
                    a {
                        &.wrap{
                            background-color: var(--variable-color4);
                        }
                        path{
                            fill: var(--white-bg);
                        }
                    }
                }
                a{
                    &.wrap{
                        width: 30px;
                        height: 30px;
                        display: flex;
                        align-items: center;
                        border-radius: 100px;
                        justify-content: center;
                        background-color: rgba(255, 255, 255, 0.1);
                        transition: all 0.4s;
                    }
                    &.wrap2{
                        position: relative;
                        &::before{
                            content: "";
                            position: absolute;
                            bottom: 0;
                            left: auto;
                            right: 0;
                            height: 1px;
                            width: 0;
                            background-color: var(--light-color);
                            transition: all .3s ease;
                        }
                        &:hover::before{
                            left: 0;
                            right: auto;
                            width: 100%;
                        }
                    }
                }
                
            }
        }
    }
}